.swiper {
  width: 100%;
  position: relative;
  //.swiper-wrapper {
  //  //display: inline-block;
  //}
  .swiper-slide {
    img {
      width: 100%;
      display: block;
    }
  }
  .swiper-container {
    width: 100%;
    height: 100%;
  }
  .pagination {
    position: absolute;
    left: 0;
    text-align: center;
    bottom: 5px;
    width: 100%;
    z-index: 10;
  }
  .swiper-pagination-switch {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    background: #999;
    box-shadow: 0px 1px 2px #555 inset;
    margin: 0 3px;
    cursor: pointer;
  }
  .swiper-active-switch {
    background: #fff;
  }
}
