.viewImage {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background: #000;
  .mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: #000;
    z-index: 1;
  }
  #viewSwiper {
    overflow: visible;
  }
  .swiper {
    position: relative;
    z-index: 99;
    top: 50%;
    transform: translateY(-50%);
  }
  .swiper-wrapper {
    display: none;
  }
  .img {
    width: 100%;
    display: block;
  }
}
