@import "../vendor/normalize";
@import "../vendor/weui/modal";
@import "../vendor/weui/cell";
@import "../util/mixins";
@import "../component/downloadApp";
@import "../component/loading";
@import "../component/swiper";
@import "../vendor/swiper";

body {
  background: #efeff5;
}

.baseInfo {
  margin-top: 0;
  &:before {
    width: 0;
    height: 0;
  }
  &:after {
    left: 10px;
  }
  .weui_cell_hd {
    padding-left: 5px;
    margin-right: 10px;
  }
  .logo {
    width: 56px;
    height: 56px;
    border-radius: 10px;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .info {
    height: 56px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    word-break: break-all;
    .name {
      font-size: 15px;
      height: 30px;
      line-height: 15px;
      overflow: hidden;
      color: #404040;
      margin-bottom: 10px;
    }
    .address {
      max-height: 32px;
      font-size: 12px;
      color: #9c9c9c;
      position: relative;
      text-indent: 12px;
      .icon {
        width: 18px;
        height: 16px;
        background: url('../../img/slice/serviceCenter/businessIcon.png');
        position: absolute;
        left: 0;
        top: 0;
      }
      .text {
        display: inline-block;
      }
    }
  }
}

.businessTags {
  padding: 0 10px;
  display: flex;
  align-items: stretch;
  background: #fff;
  margin-bottom: 10px;
  .tag {
    flex: 1;
    margin:10px 5px;
  }
  .icon {
    width: 100%;
    margin-bottom: 5px;
    .height {
      width: 100%;
      height: 0;
      padding-bottom: 100%;
    }
  }
  .label {
    font-size: 12px;
    color: #9c9c9c;
    text-align: center;
  }
  .hot {
    background: url('../../img/slice/serviceCenter/isHot.png') no-repeat;
  }
  .hotOn {
    background: url('../../img/slice/serviceCenter/isHotOn.png') no-repeat;
  }
  .famous {
    background: url('../../img/slice/serviceCenter/isFamous.png') no-repeat;
  }
  .famousOn {
    background: url('../../img/slice/serviceCenter/isFamousOn.png') no-repeat;
  }
  .free {
    background: url('../../img/slice/serviceCenter/isFree.png') no-repeat;
  }
  .freeOn {
    background: url('../../img/slice/serviceCenter/isFreeOn.png') no-repeat;
  }
  .return {
    background: url('../../img/slice/serviceCenter/isReturn.png') no-repeat;
  }
  .returnOn {
    background: url('../../img/slice/serviceCenter/isReturnOn.png') no-repeat;
  }
  .authentic {
    background: url('../../img/slice/serviceCenter/isAuthentic.png') no-repeat;
  }
  .authenticOn {
    background: url('../../img/slice/serviceCenter/isAuthenticOn.png') no-repeat;
  }
}

.introduce {
  padding: 10px;
  background: #fff;
  margin-bottom: 10px;
  ::-webkit-scrollbar {
    width: 0;
  }
  .text {
    font-size: 14px;
    color: #404040;
    line-height: 1.2;
    word-break: break-all;
  }
  .label {
    color: #9c9c9c;
  }
  .scrollbar {
    width: 100%;
    overflow-x: auto;
  }
  .businessImg {
    margin-top: 15px;
    overflow-x: auto;
    display: flex;
    .img {
      width: 105px;
      height: 80px;
      background-size: cover;
      background-repeat: no-repeat;
      margin-right: 15px;
      border-radius: 5px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.tabs {
  width: 100%;
  background: #fff;
  .tabHeader {
    position: relative;
    border-bottom: 1px solid #efeff5;
  }
  .tabItem {
    width: 90%;
    margin: 0 auto;
    display: flex;
    list-style: none;
    padding: 10px 0;
    li {
      flex: 1;
      text-align: center;
      font-size: 15px;
      color: #9c9c9c;
    }
  }
  .tabLine {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 1px;
    height: 1px;
    background: #2baa8f;
    transition: left 0.25s;
  }
  .label {
    display:inline-block;
  }
}

#businessService {
  .swiper-slide {
    display: none;
  }
  .weui_cells {
    margin-top: 0;
    &:after, &:before {
      width: 0;
      height: 0;
    }
  }
}

.cards {
  .weui_cell_hd {
    width: 61%;
    margin-right: 15px;
  }
  .weui_cell_bd {
    align-self: flex-end;
  }
  .card {
    width: 100%;
    position: relative;
    .img {
      width: 100%;
      display: block;
    }
    .business {
      position: absolute;
      right: 5px;
      bottom: 5px;
      .businessIcon {
        width: 18px;
        height: 16px;
        background: url('serviceCenter/businessIconWhite.png') no-repeat;
        background-size: 100%;
        position: absolute;
        right: 0;
        top: 0;
      }
      .businessName {
        padding-right: 22px;
        font-size: 13px;
        color: #fff;
      }
    }
    .money {
      width: 79%;
      position: absolute;
      left: 50%;
      top: 45%;
      transform: translate(-50%, -50%);
      padding-left: 21%;
      text-align: center;
      .num, .label {
        float: left;
      }
      .label {
        position: relative;
        top: 8px;
      }
      .num, .rmb {
        color: #fff;
        font-weight: bold;;
      }
      .num {
        width: 100%;
        font-size: 25px;
        margin-right: 5px;
      }
      .rmb {
        font-size: 14px;
      }
      .type {
        font-size: 12px;
        color: #fff;
        background: #ecab36;
      }
    }
  }
  .info {
    font-size: 12px;
    color: #c0c0c0;
  }
  .getCard {
    font-size: 16px;
    color: #2baa8f;
    border: 1px solid #2baa8f;
    border-radius: 5px;
    padding: 3px 15px;
    text-decoration: none;
    margin-top: 10px;
    display: inline-block;
  }
}

.serviceContent {
  font-size: 14px;
  color: #404040;
  padding: 10px;
  line-height: 1.3;
  padding-bottom: 60px;
}

.mallGoods {
  .goodsImg {
    background-size: cover;
    border-radius: 5px;
    margin-right: 10px;
  }
  .goodsName {
    font-size: 15px;
    color: #404040;
    @include ellipsis();
  }
  .tags {
    font-size: 15px;
    color: #9c9c9c;
    text-indent: 20px;
    position: relative;
    @include ellipsis();
  }
  .tagIcon {
    width: 16px;
    height: 16px;
    background: url('serviceCenter/tag.png') no-repeat;
    background-size: 100%;
    position: absolute;
    top: 3px;
    left: 0;
  }
  .weui_cell {
    padding: 10px 0 10px 10px;
  }
  .weui_cell_bd {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
  }
  .info {
    display: flex;
    justify-content: space-between;
    padding-right: 5px;
  }
  .expFee, .sales, .clicks {
    width: 33.33%;
    color: #c0c0c0;
    border-right: 1px solid #efeff5;
    display: flex;
    justify-content: space-between;
    @include ellipsis();
  }
  .clicks {
    border-right: 0;
  }
  .text {
    color: #9c9c9c;
  }
  @media screen and (min-width: 320px)  {
    .goodsImg {
      width: 90px;
      height: 60px;
      margin-right: 5px;
    }
    .weui_cell_bd {
      height: 60px;
    }
    .expFee, .sales, .clicks {
      font-size: 12px;
      padding: 0 3px;
    }
    .label {
      margin-right: 1px;
    }
    .expFee {
      padding: 0 3px 0 0;
    }
  }
  @media screen and (min-width: 321px) and (max-width: 375px) {
    .goodsImg {
      width: 90px;
      height: 60px;
      margin-right: 10px;
    }
    .weui_cell_bd {
      height: 60px;
    }
    .expFee, .sales, .clicks {
      font-size: 12px;
      padding: 0 5px;
    }
    .label {
      margin-right: 2px;
    }
    .expFee {
      padding: 0 5px 0 0;
    }
  }
  @media screen and (min-width: 375px) {
    .goodsImg {
      width: 120px;
      height: 80px;
      margin-right: 10px;
    }
    .weui_cell_bd {
      height: 80px;
    }
    .expFee, .sales, .clicks {
      font-size: 13px;
      padding: 0 7px;
    }
    .label {
      margin-right: 2px;
    }
    .expFee {
      padding: 0 7px 0 0;
    }
  }
}

.noData {
  width: 90% !important;
  margin: 10px auto;
}

.tel {
  width: 100%;
  height: 50px;
  line-height: 50px;
  border: 1px solid #2baa8f;
  display: flex;
  position: fixed;
  bottom: 0;
  background: #fff;
  z-index: 1;
  .label {
    font-size: 16px;
    color: #2baa8f;
    padding: 0 20px;
  }
  .text {
    background: #2baa8f;
    text-align: center;
    flex: 1;
  }
  .phone {
    font-size: 16px;
    color: #fff;
    text-decoration: none;
  }
}
